@tailwind base;
@tailwind components;
@tailwind utilities;

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background: #373b99;
}

.success {
  font-family: system-ui, sans-serif;
  font-size: 2rem;
  background-color: lightgray;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  border-style: solid;
  color: green;
}
.failure {
  font-family: system-ui, sans-serif;
  font-size: 2rem;
  background-color: lightgray;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  border-style: solid;
  color: red;
}

.main-heading {
  background: linear-gradient(to bottom, #5b5bb3, #141e30);
  border-radius: 4rem;
  margin-block: 3rem;
  padding: 1rem 2rem;
  font-family: 'Inter', sans-serif;
  font-size: 4rem;
  line-height: 58px;
  color: #ffffff;
}

.filter-text,
.add-person {
  background: linear-gradient(to right, #f12711, #f5af19);
  border-radius: 4rem;
  padding: 1rem 2.5rem;
  font-family: 'Inter', sans-serif;
  color: #ffffff;
}

